.article-nav {
    margin: 20px 0;

    .container {
        max-width: 985px;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 30px;
    }

    .article-nav__link {
        background: #F7F6F6;
        padding: 20px;
        font-weight: 800;
        font-size: 23px;
        line-height: 120%;
        letter-spacing: 0.08em;
        color: #2D2D2D;
        transition: all 0.3s ease-in-out;

        &:hover {
            background: #e8e8e8;
        }

        span {
            font-weight: 800;
            font-size: 15px;
            line-height: 150%;
            letter-spacing: 0.08em;
            text-transform: uppercase;
            color: #BABEC1;
            display: block;
            margin-bottom: 10px;
        }
    }
}

@media (max-width: 800px) {
    .article-nav .container {
        grid-template-columns: 1fr;
    }
}

@media (max-width: 580px) {
    .article-nav .article-nav__link {
        font-size: 20px;

        span {
            font-size: 14px;
            margin-bottom: 5px;
        }
    }

    .article-nav .container {
        gap: 20px;
    }
}