.severin {
    margin-bottom: 63px;
    position: relative;

    @media(max-width:1023px) {
        margin-bottom: 35px;
    }
}

.severin__bg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: -1;

    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: left;
    }
}


.severin__inner {
    height: 801px;
    display: flex;
    justify-content: flex-end;
    align-items: center;

    @media(max-width:1919px) {
        height: 660px;
    }

    @media(max-width:1023px) {
        height: auto;
    }
}

.severin__info {
    padding: 30px;
    max-width: 618px;

    background: rgba(#004080, .8);
    border-radius: 4px;

    @media(max-width:1439px) {
        padding: 15px;
        max-width: 510px;
    }

    @media(max-width:1023px) {
        margin: 50px 0;
        padding: 10px;
    }
}

.severin__title {
    color: #FFFFFF;
    margin-bottom: 25px;

    @media(max-width:1439px) {
        margin-bottom: 15px;
    }

    @media(max-width:1023px) {
        text-align: center;
    }
}

.severin__descr {
    color: #FFFFFF;
    margin-bottom: 25px;

    @media(max-width:1439px) {
        margin-bottom: 15px;
    }

    @media(max-width:1023px) {
        text-align: center;
    }
}
