// _intro 



.intro {
    position: relative;
    margin-bottom: 32px;
}

.intro__slide {
    width: 100%;
    height: 750px;
    display: flex;
    flex-direction: column;
    justify-content: center;

    position: relative;

    @media(max-width:1650px) {
        height: 650px;
    }

    @media(max-width:1439px) {
        height: 550px;
    }

    @media(max-width:767px) {
        height: 423px;
    }
}

.intro__img {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    z-index: -1;

    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;

        @media(min-width:1921px) {
            object-position: top;
        }
    }

    &::before {
        content: '';
        background: #000000;
        opacity: 0.35;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
    }
}

.intro__title {
    max-width: 840px;
    margin-bottom: 50px;

    color: #FFFFFF;
}

.intro__nav {
    display: flex;
    align-items: center;
}

.intro__info-btn {
    width: fit-content;
    overflow: hidden;
    margin-left: 50px;

    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: 0.01em;
    color: #FFFFFF;

    position: relative;

    &::before {
        content: '';
        height: 1px;
        background-color: #fff;
        transition: .2s linear;
        
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
    }

    @media(min-width:1024px) {
        &:hover::before {
            transform: translateX(-110%);
        }
    }

    @media(max-width:767px) {
        margin-left: auto;
    }
}

.intro-button-next {
    width: 51px;
    height: 51px;

    background-image: url("../img/intro-arrow.svg");

    position: absolute;
    top: 50%;
    right: 30px;
    z-index: 2;
    transform: translateY(-50%) rotate(180deg);

    @media(max-width:1650px) {
        display: none;
    }
}

.intro-button-prev {
    width: 51px;
    height: 51px;

    background-image: url("../img/intro-arrow.svg");

    position: absolute;
    top: 50%;
    left: 30px;
    z-index: 2;
    transform: translateY(-50%);

    @media(max-width:1650px) {
        display: none;
    }
}