.btn {
    display: flex;
    align-items: center;
    width: fit-content;
    padding: 15px 30px;

    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: 0.01em;
    color: #FFFFFF;

    transition: .2s linear;

    &--trans {
        border-radius: 4px;
        overflow: hidden;

        position: relative;

        box-shadow: inset 0 0 20px rgba(255, 255, 255, 0);
        outline: 1px solid;
        outline-color: rgba(255, 255, 255, .5);
        outline-offset: 0px;

        &::before {
            content: '';
            border-radius: 4px;
            border: 1px solid;
            transition: .2s linear;
            opacity: 0;

            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
        }

        &:hover {
            outline-color: rgba(255, 255, 255, 0);
            outline-offset: 15px;

            &::before {
                opacity: 1;
            }
        }
    }

    &--black {
        background: #2D2D2D;
        border-radius: 4px;

        @media(min-width:1024px) {
            &:hover {
                background: #004080;
            }
        }
    }

    &--blue {
        width: fit-content;
        min-height: 24px;
        overflow: hidden;
        color: #004080;
        position: relative;
        padding: 0;
        height: fit-content;

        &::before {
            content: '';
            height: 1px;
            background-color: #004080;
            transition: .2s linear;
            position: absolute;
            right: 0;
            bottom: 0;
            width: 100%;
        }

        @media(min-width:1024px) {
            &:hover::before {
                width: 0;
                text-shadow: .5px 0 0 currentColor;
            }
        }
    }

    &--line {
        width: fit-content;
        color: #2D2D2D;
        position: relative;
        padding: 0;
        height: fit-content;

        &::before {
            content: '';
            height: 1px;
            background-color: #2D2D2D;
            transition: .2s linear;
            position: absolute;
            right: 0;
            bottom: 0;
            width: 100%;
        }

        @media(min-width:1024px) {
            &:hover::before {
                width: 0;
                text-shadow: .5px 0 0 currentColor;
            }
        }
    }

}