.theme-intro {
    margin-bottom: 30px;

    @media(max-width:767px) {
        margin-bottom: 27px;
    }
}

.theme-intro__title {
    color: #2D2D2D;
    text-align: center;
    margin-bottom: 30px;
}

.theme-intro__board {
    display: block;
    height: 700px;

    border-radius: 4px;
    overflow: hidden;
    position: relative;

    @media(max-width:1470px) {
        height: auto;
    }



    @media(max-width:600px) {
        width: 335px;
        height: 421px;
        margin: 0 auto;
    }

    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    .theme-intro-pc {
        @media(max-width:600px) {
            display: none;
        }
    }

    .theme-intro-mob {
        display: none;

        @media(max-width:600px) {
            display: block;
        }
    }
}

.theme-intro__dropdown {
    position: absolute;
    top: 26%;
    right: 18%;

    z-index: 2;

    @media(max-width:600px) {
        top: 179px;
        right: 91px;
    }

    &::before {
        content: '';
        width: 43px;
        height: 43px;

        background: #2D2D2D;
        opacity: 0.5;
        border-radius: 50%;

        position: absolute;
        top: 0;
        right: 0;

        @media(max-width:1023px) {
            width: 23.68px;
            height: 23.68px;
        }

        @media(max-width:600px) {
            width: 25px;
            height: 25px;
        }
    }

    &::after {
        content: '';
        width: 17px;
        height: 17px;

        background: #FFFFFF;
        border-radius: 50%;

        position: absolute;
        top: 13px;
        right: 13px;

        @media(max-width:1023px) {
            width: 9.36px;
            height: 9.36px;
            top: 7.16px;
            right: 7.16px;
        }

        @media(max-width:600px) {
            width: 9.88px;
            height: 9.88px;

            top: 7.56px;
            right: 7.56px;
        }
    }

    &:hover {
        .theme-intro__link {
            opacity: 1;
            visibility: visible;
        }
    }
}

.theme-intro__link {
    display: block;
    width: 209px;
    padding: 10px;

    opacity: 0;
    visibility: hidden;
    transition: .3s linear;

    position: absolute;
    top: 36px;
    right: 21px;

    background: #FFFFFF;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
    border-radius: 4px;

    @media(max-width:1023px) {
        width: 172px;
        top: 11px;
    }

    @media(max-width:600px) {
        top: 17px;
        right: 17px;
    }

    &::before {
        content: '';
        background-image: url('../img/back-page.svg');
        background-repeat: no-repeat;
        background-size: cover;

        width: 24px;
        height: 24px;

        position: absolute;
        top: 10px;
        right: 21px;
        transform: rotate(180deg);

        @media(max-width:1023px) {
            top: 10px;
            right: 10px;
        }

        @media(max-width:600px) {
            top: 7.5px;
            right: 15px;
        }
    }
}

.theme-intro__name {
    margin-bottom: 13px;

    color: #000000;

    @media(max-width:1023px) {
        margin-bottom: 10px;
    }
}

.theme-intro__model {
    margin-bottom: 10px;

    color: #2d2d2d;
}

.theme-intro__price {
    color: #000000;
}