* {
    font-family: "Gilroy", sans-serif;
    color: #2D2D2D;
}

body {

    &.scroll-hide {
        overflow: hidden;
    }
}

.container {
    width: 100%;
    max-width: 1472px;
    margin: 0 auto;
    padding: 0 26px;

    @media(max-width:767px) {
        padding: 0 20px;
    }
}

.page {
    position: relative;
    overflow: hidden;
    min-height: 100vh;

    &--cart {
        @media(min-width:1366px) {
            overflow: unset;
        }
    }

    &.scroll-hide {
        overflow: hidden;
        height: calc(var(--vh, 1vh) * 100);
    }
}

.h1 {
    font-weight: 800;
    font-size: 100px;
    line-height: 100%;
    letter-spacing: 0.03em;

    @media(max-width:1500px) {
        font-size: 70px;
    }

    @media(max-width:767px) {
        font-size: 40px;
    }
}

.h2 {
    font-weight: 800;
    font-size: 50px;
    line-height: 110%;
    letter-spacing: 0.08em;

    @media(max-width:1500px) {
        font-size: 35px;
    }

    @media(max-width:767px) {
        font-size: 30px;
    }
}

.h3 {
    font-weight: 800;
    font-size: 35px;
    line-height: 120%;
    letter-spacing: 0.08em;

    @media(max-width:1500px) {
        font-size: 25px;
    }

    @media(max-width:767px) {
        font-size: 23px;
    }
}

.h4 {
    font-weight: 800;
    font-size: 23px;
    line-height: 120%;
    letter-spacing: 0.04em;

    @media(max-width:1500px) {
        font-size: 20px;
    }

    @media(max-width:767px) {
        font-size: 18px;
    }
}

.h5 {
    font-weight: 800;
    font-size: 18px;
    line-height: 120%;
    letter-spacing: 0.04em;

    @media(max-width:767px) {
        font-size: 15px;
    }
}

.h6 {
    font-weight: 800;
    font-size: 13px;
    line-height: 120%;
    letter-spacing: 0.08em;
}

.body-l {
    font-weight: 400;
    font-size: 23px;
    line-height: 150%;
    letter-spacing: 0.08em;

    @media(max-width:1500px) {
        font-size: 20px;
    }
}

.body-m {
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;
    letter-spacing: 0.08em;

    @media(max-width:1500px) {
        font-size: 16px;
    }
}

.body-m-bold {
    font-weight: 800;
    font-size: 18px;
    line-height: 150%;
    letter-spacing: 0.08em;

    @media(max-width:1500px) {
        font-size: 16px;
    }
}

.body-s {
    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    letter-spacing: 0.08em;

    @media(max-width:1500px) {
        font-size: 12px;
        line-height: 130%;
    }
}

.body-s-bold {
    font-weight: 800;
    font-size: 14px;
    line-height: 150%;
    letter-spacing: 0.1em;

    @media(max-width:1500px) {
        font-size: 12px;
    }
}

.body-xs {
    font-weight: 400;
    font-size: 12px;
    line-height: 150%;
    letter-spacing: 0.08em;

    @media(max-width:1500px) {
        font-size: 10px;
    }
}

.title {
    font-weight: 800;
    font-size: 35px;
    line-height: 120%;
    letter-spacing: 0.08em;
    color: #2D2D2D;

    &.title-center {
        text-align: center;
    }

    @media(max-width:1500px) {
        font-size: 25px;
    }

    @media(max-width:767px) {
        font-size: 23px;
    }

    span {
        font-weight: inherit;
        font-size: inherit;
        line-height: inherit;
        letter-spacing: inherit;
        color: #BABEC1;
    }
}

.progressbar {
    height: 3px !important;
    background: #BABEC1;
    top: unset !important;
    bottom: 0;

    .swiper-pagination-progressbar-fill {
        background: #2D2D2D;
    }
}

.input,
.select {
    width: 100%;
    height: 54px;
    padding: 16.5px 10px;

    border: 1px solid #BABEC1;
    border-radius: 4px;

    color: #2D2D2D;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: 0.08em;

    &::placeholder {
        color: #C6CACE;
    }

    @media (max-width: 580px) {
        height: 44px;
        font-size: 14px;
        padding: 9.5px 10px;
    }
}

.input-wrap {
    .input-help-text {
        font-weight: 400;
        font-size: 12px;
        line-height: 150%;
        letter-spacing: 0.08em;
        color: #000000;
        margin-top: 10px;
    }

    .input-title {
        font-weight: 800;
        font-size: 14px;
        line-height: 120%;
        letter-spacing: 0.05em;
        text-transform: uppercase;
        color: #2D2D2D;
        margin-bottom: 10px;

        span {
            color: #E7312F;
        }
    }
}

.input--small {
    width: 50%;

    @media(max-width:767px) {
        width: 100%;
    }
}

.select {
    color: #C6CACE;

    &.active {
        color: #2D2D2D;
    }
}

picture {
    display: block;
    height: 100%;

    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.select-wrap {
    .select-title {
        font-weight: 800;
        font-size: 14px;
        line-height: 120%;
        letter-spacing: 0.05em;
        text-transform: uppercase;
        color: #2D2D2D;
        margin-bottom: 10px;

        span {
            color: #E7312F;
        }
    }
}

.select-block-wrap {
    position: relative;



    &.active {
        &::before {
            transform: translateY(-50%) rotateX(180deg);
        }
    }

    &::before {
        content: '';
        position: absolute;
        background-image: url('../img/arrow-select.svg');
        background-repeat: no-repeat;
        background-size: cover;
        width: 24px;
        height: 24px;
        right: 15px;
        top: 50%;
        transform: translateY(-50%);
        pointer-events: none;
        transition: .2s linear;
    }

    select {
        cursor: pointer;
        padding-right: 40px;
    }
}

.amount-block {
    border: 1px solid #BABEC1;
    border-radius: 4px;
    width: 80px;
    height: 55px;
    background-color: transparent;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: 0.1em;
    color: #2D2D2D;
    padding-right: 25px !important;
    padding-left: 21px;
    cursor: pointer;
}


.texarea-wrap {
    .texarea-title {
        font-weight: 800;
        font-size: 14px;
        line-height: 120%;
        letter-spacing: 0.05em;
        text-transform: uppercase;
        color: #2D2D2D;
        margin-bottom: 10px;

        span {
            color: #E7312F;
        }
    }

    textarea {
        border: 1px solid #BABEC1;
        border-radius: 4px;
        height: 200px;
        width: 100%;
        resize: none;
        padding: 16px 10px;
        font-weight: 400;
        font-size: 16px;
        line-height: 24px;
        letter-spacing: 0.01em;
        color: #000000;

        &::placeholder {
            color: #BABEC1;
        }
    }
}




.breadcrumbs-list {
    margin: 10px 0 20px 0;
    position: relative;
    z-index: 5;
    border-top: 1px solid #BABEC1;
    padding-top: 10px;

    &__item {
        display: inline-block;
        padding-right: 25px;
        position: relative;

        a,
        span {
            font-weight: 400;
            font-size: 12px;
            line-height: 150%;
            letter-spacing: 0.08em;
            color: #2D2D2D;
            transition: all .3s;
        }

        a {
            color: #BABEC1;

            &:hover {
                text-shadow: .5px 0 0 currentColor;
            }
        }

        &:last-child::after {
            display: none;
        }

        &::after {
            position: absolute;
            content: '/';
            top: 3px;
            right: 6px;
            font-weight: 400;
            font-size: 12px;
            line-height: 15px;
            letter-spacing: 0.05em;
            text-transform: uppercase;
            color: #BABEC1;
        }
    }
}


.back-page {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: 0.01em;
    color: #2D2D2D;
    display: flex;
    align-items: center;
    // text-decoration: underline;
    position: relative;

    &::before {
        content: '';
        background-image: url('../img/back-page.svg');
        background-repeat: no-repeat;
        background-size: cover;
        width: 24px;
        height: 24px;
        display: block;
        margin-right: 10px;
    }

    &::after {
        content: '';
        width: calc(100% - 34px);
        height: 1px;
        background-color: #2D2D2D;
        transition: .2s linear;
        position: absolute;
        left: 34px;
        right: 0;
        bottom: 2px;
    }

    @media(min-width:1024px) {
        &:hover {
            &::after {
                width: 0%;
                left: 34px;
                right: auto;
            }
        }
    }

    &--revers {
        padding-right: 34px;

        &::before {
            margin-right: 0;

            position: absolute;
            right: 0;
            transform: rotate(180deg);
        }

        &::after {
            left: unset;
            right: 34px;
        }

        @media(min-width:1024px) {
            &:hover {
                &::after {
                    width: 0%;
                    left: auto;
                    right: 34px;
                }
            }
        }
    }
}


.choices__inner {
    background-color: transparent;
    border-radius: 0;
    width: auto;
    border: none;
    font-weight: 800;
    font-size: 15px;
    line-height: 120%;
    letter-spacing: 0.08em;
    color: #2D2D2D;
    padding: 0;
    padding-right: 40px;
    min-height: auto;
    padding-bottom: 0 !important;
}

.choices__placeholder {
    opacity: 1;
}

.choices__input {
    display: none !important;
}

.choices__list--dropdown .choices__item {
    font-weight: 600;
    font-size: 14px;
    line-height: 120%;
    letter-spacing: 0.08em;
    color: #2D2D2D;
    padding-right: 55px;
}

.choices__list--dropdown .choices__item--selectable:after {
    display: none;
}

.choices__list--single {
    padding: 0;
}

.choices[data-type*=select-one]:after {
    border: none;
    background-image: url('../img/select-arrow.svg');
    width: 12px;
    height: 5px;
    top: 8px;
    right: 0;
    margin: 0 !important;
}

.choices[data-type*=select-one].is-open:after {
    transform: rotateX(180deg);
}

.choices__list--dropdown {
    margin: 0 !important;
    margin-top: 10px !important;
    border-radius: 0;
    left: -1px;
}



.page-status {
    width: 356px;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    align-items: center;

    @media(max-width:767px) {
        max-width: 335px;
    }
}

.page-status-text {
    margin-bottom: 30px;

    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: 0.01em;
    color: #000000;

    @media(max-width:767px) {
        margin-bottom: 15px;
    }
}

.page-status-bar {
    height: 3px;
    width: 100%;
    margin-bottom: 30px;

    background-color: #BABEC1;

    position: relative;

    @media(max-width:767px) {
        margin-bottom: 15px;
    }
}

.page-status-line {
    width: 114px;
    background-color: #2D2D2D;

    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
}


.status-info {
    background: #FFF4D7;
    border-radius: 4px;
    padding: 20px;
    display: flex;
    align-items: center;

    &::before {
        content: '';
        background-repeat: no-repeat;
        background-size: cover;
        width: 24px;
        height: 24px;
        display: block;
        margin-right: 20px;
    }

    &.status-orange {
        &::before {
            background-image: url('../img/i.svg');
        }

        background: #FFF4D7;
        color: #E1BE63;
    }

    &.status-green {
        background: #F3F8F2;
        color: #67B160;

        &::before {
            background-image: url('../img/icon-added.svg');
        }
    }
}