// thematic-block


.thematic-block {
    margin-bottom: 57px;
}

.thematic-block__item {
    display: flex;
    justify-content: space-between;

    @media(min-width:768px) {
        &:nth-child(even) {
            flex-direction: row-reverse;
        }
    }

    @media(max-width:767px) {
        flex-direction: column;
    }

    &+& {
        margin-top: 60px;

        @media(max-width:1439px) {
            margin-top: 30px;
        }
    }

    .product__name {
        font-size: 13px;
        line-height: 120%;
        letter-spacing: 0.08em;
    }

    &--without-products {
        .thematic-block__info {
            margin-bottom: 0;
        }

        .thematic-block__text {
            margin-bottom: 0;
        }
    }
}

.thematic-block__photo {
    width: calc(50% - 15px);
    display: flex;
    justify-content: center;
    align-items: center;

    @media(max-width:1023px) {
        width: calc(50% - 7.5px);
    }

    @media(max-width:767px) {
        width: 100%;
        margin-bottom: 20px;
    }
}

.thematic-block__img {
    width: 454px;
    height: 453px;
    border-radius: 4px;
    overflow: hidden;

    @media(max-width:1439px) {
        width: 354px;
        height: 353px;
    }

    @media(max-width:1023px) {
        width: 284px;
        height: 283px;
    }

    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.thematic-block__content {
    width: calc(50% - 15px);
    display: flex;
    flex-direction: column;
    justify-content: center;

    @media(max-width:1023px) {
        width: calc(50% - 7.5px);
    }

    @media(max-width:767px) {
        width: 100%;
    }
}

.thematic-block__title {
    margin-bottom: 20px;
    color: #2D2D2D;

    @media(max-width:767px) {
        text-align: center;
    }
}

.thematic-block__text {
    margin-bottom: 20px;
    color: #2D2D2D;

    @media(max-width:767px) {
        text-align: center;
    }
}

.thematic-block__info {
    padding: 50px;

    @media(max-width:1439px) {
        padding: 20px;
    }

    @media(max-width:1023px) {
        padding: 0;
        margin-bottom: 20px;
    }
}

.thematic-block__wrapper {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 30px;

    @media(max-width:1439px) {
        grid-gap: 15px;
    }

    @media(max-width:1023px) {
        max-width: 380px;
        grid-template-columns: repeat(2, 1fr);

        .product {
            &:nth-child(n+3) {
                display: none;
            }
        }
    }

    @media(max-width:767px) {
        margin: 0 auto;
    }
}

.thematic-block__btn {
    @media(max-width:767px) {
        margin: 0 auto;
    }
}