.ban-block {
    position: relative;
    margin-bottom: 30px;

    img {
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        object-fit: cover;
        z-index: -1;
    }

    .container {
        font-weight: 800;
        font-size: 50px;
        line-height: 110%;
        letter-spacing: 0.08em;
        color: #FFFFFF;
        text-shadow: 0px 10px 10px rgba(0, 0, 0, 0.25);
        display: flex;
        justify-content: center;
        align-items: center;
        height: 100%;
        min-height: 800px;
        text-align: center;
    }
}

@media (max-width: 950px) {
    .ban-block .container {
        min-height: 500px;
        font-size: 35px;
    }
}

@media (max-width: 700px) {
    .ban-block .container {
        min-height: 350px;
        font-size: 25px;
    }
}