.blog__title {
    font-weight: 800;
    font-size: 50px;
    line-height: 110%;
    letter-spacing: 0.08em;
    color: #2D2D2D;
    text-align: center;
    margin: 30px 0;
}

@media (max-width: 1300px) {
    .blog__title {
        font-size: 35px;
    }
}

@media (max-width: 580px) {
    .blog__title {
        margin: 20px 0;
    }
}