.category-nav {
    user-select: none;

    &--slider {
        margin-bottom: 53px;

        @media(max-width:767px) {
            margin-bottom: 30px;
        }
    }

    &--theme {
        margin-bottom: 30px;

        @media(max-width:767px) {
            margin-bottom: 27px;
        }

        .category-nav__item {
            max-width: 291.61px;

            @media(max-width:767px) {
                max-width: 336.81px;
                height: 242.55px;
            }
        }
    }

    &--overview {
        max-width: 1120px;
        margin: 0 auto;

        .category-nav__item {
            width: 330.61px;
            height: 238.09px;

            @media(max-width:767px) {
                max-width: 336.81px;
                height: 242.55px;
            }
        }
    }
}

.category-nav__wrapper {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin: 0 -15px;

    .category-nav__item {
        margin: 0 15px 30px;

        @media(max-width:767px) {
            margin-bottom: 20px;
        }
    }
}

.category-nav__slider {
    .swiper-wrapper {
        margin-bottom: 30px;
    }
}

.category-nav__item {
    display: block;
    width: 100%;
    height: 211px;

    border-radius: 4px;
    overflow: hidden;

    position: relative;

    &::before {
        content: '';
        background: #2D2D2D;
        opacity: 0.3;

        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: 2;
    }

    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: .3s linear;
    }

    @media(min-width:1024px) {
        &:hover {
            img {
                transform: scale(1.1);
            }
        }
    }

    @media(max-width:767px) {
        height: 180px;
    }
}

.category-nav__name {
    color: #FFFFFF;
    text-align: center;

    position: absolute;
    left: 0;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    z-index: 3;
}