// bestseller


.bestseller {
    margin-bottom: 60px;
    /* 
    @media(max-width:1439px) {
        margin-bottom: 60px;
    } */

    @media(max-width:1023px) {
        margin-bottom: 35px;
    }

    &--big {
        .product__name {
            font-size: 13px;
            line-height: 120%;
            letter-spacing: 0.08em;
        }
    }
}


.bestseller__title {
    margin-bottom: 60px;
    text-align: center;

    @media(max-width:1439px) {
        margin-bottom: 35px;
    }
}

.bestseller__list {
    @media(min-width:580px) {
        overflow: visible;

        .swiper-wrapper {
            display: grid;
            grid-template-columns: repeat(6, 1fr);
            grid-gap: 30px;

            @media(max-width:1439px) {
                grid-gap: 15px;
                grid-template-columns: repeat(5, 1fr);

                .product:nth-child(n+6) {
                    display: none;
                }
            }

            @media(max-width:1023px) {
                grid-template-columns: repeat(4, 1fr);

                .product:nth-child(n+5) {
                    display: none;
                }
            }

            @media(max-width:710px) {
                grid-template-columns: repeat(3, 1fr);

                .product:nth-child(n+4) {
                    display: none;
                }
            }

            &--short {
                max-width: 1055.25px;
                margin-left: auto;
                margin-right: auto;
                grid-template-columns: repeat(3, 1fr);

                grid-gap: 30px;

                @media(max-width:767px) {
                    grid-template-columns: repeat(2, 1fr);

                    .product:nth-child(n+3) {
                        display: none;
                    }
                }
            }
        }
    }

    @media(max-width:579px) {
        margin-right: -20px;
        padding-right: 20px;

        .swiper-wrapper {
            margin-bottom: 30px;
            user-select: none;
        }

        .product {
            height: 414px;
        }

        .progressbar {
            width: calc(100% - 20px) !important;
        }
    }
}

.bestseller__progressbar {
    @media(min-width:580px) {
        display: none;
    }
}


.bestseller__btn {
    margin: 45px auto 0;

    @media(max-width:1023px) {
        margin-top: 26px;
    }

    @media(max-width:767px) {
        margin-top: 30px;
    }
}