.card-mini {
    display: flex;
    align-items: center;
    flex-direction: column;
    background: #F7F6F6;
    border-radius: 4px;
    padding: 15px 20px;
}

.card-mini__img {
    width: 100%;
    height: 279px;
    display: flex;
    justify-content: center;
    align-items: center;

    img {
        max-width: 100%;
        max-height: 100%;
        object-fit: cover;
    }
}

.card-mini__title {
    font-weight: 800;
    font-size: 18px;
    line-height: 120%;
    letter-spacing: 0.04em;
    color: #2D2D2D;
    margin-top: 10px;
}

.card-mini__code {
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;
    letter-spacing: 0.08em;
    color: #2D2D2D;
    margin-top: 5px;
}

.card-mini__price {
    font-weight: 800;
    font-size: 14px;
    line-height: 150%;
    letter-spacing: 0.1em;
    color: #2D2D2D;
    margin-top: 5px;
}

.card-mini__btn {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: 0.01em;
    color: #2D2D2D;
    margin-top: 5px;
    position: relative;

    &::before {
        position: absolute;
        content: '';
        width: 100%;
        height: 1px;
        background-color: #2D2D2D;
        left: 0;
        bottom: 0;
    }
}