.retailers {
    margin-bottom: 90px;

    @media(max-width:1439px) {
        margin-bottom: 60px;
    }

    @media(max-width:1023px) {
        margin-bottom: 30px;
    }
}

.retailers__title {
    margin-bottom: 64px;

    text-align: center;
    color: #2D2D2D;

    @media(max-width:1023px) {
        margin-bottom: 30px;
    }
}

.retailers__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    max-width: 1056px;
    margin: 0 auto;
    grid-gap: 30px;

    @media(max-width:767px) {
        grid-template-columns: repeat(1, 1fr);
    }
}

.retailers__item {
    display: flex;
    justify-content: center;
    align-items: center;

    picture {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    img {
        height: auto;
        object-fit: contain;
    }
}