.checkbox {
    display: block;
    display: flex;
    align-items: center;
    cursor: pointer;
}

.checkbox__input {
    display: none;

    &:checked+.checkbox__content::before {
        background-image: url("../img/checkbox.svg");
        background-size: cover;
        background-repeat: no-repeat;
        border-color: transparent;
    }

}

.checkbox__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;

    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: 0.01em;
    color: #000000;
    padding-left: 39px;
    position: relative;

    &::before {
        content: '';
        width: 24px;
        height: 24px;
        border: 1px solid #BABEC1;
        border-radius: 4px;
        transition: .2s linear;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        left: 0;
    }

    @media(min-width:1024px) {
        &:hover::before {
            background: #F7F6F6;
        }
    }
}

.checkbox--cirlce {
    .checkbox__input {
        &:checked+.checkbox__content {
            &::before {
                background-image: unset;
            }

            &::after {
                opacity: 1;
            }
        }
    }

    .checkbox__content {
        font-size: 18px;
        line-height: 150%;
        letter-spacing: 0.08em;

        @media(max-width:1919px) {
            font-size: 14px;
        }

        &::before {
            border-radius: 50%;
            top: 50%;
            transform: translateY(-50%);
        }

        &::after {
            content: '';
            width: 25px;
            height: 25px;
            background-image: url("../img/checkbox-cirlce.svg");
            background-size: contain;
            background-repeat: no-repeat;
            opacity: 0;
            transition: .2s linear;
            position: absolute;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
        }
    }
}

.checkbox__value {
    padding: 5px 15px;
    margin-left: 16px;

    background: #F7F6F6;
    border-radius: 20px;

    color: #BABEC1;
}