// category


.category {
    margin-bottom: 65px;

    @media(max-width:1023px) {
        margin-bottom: 36px;
    }
}

.category__title {
    margin-bottom: 55px;
    text-align: center;

    @media(max-width:1439px) {
        margin-bottom: 35px;
    }
}

.category__slider {
    position: relative;
    overflow: hidden;

    .swiper-wrapper {
        margin-bottom: 30px;

        @media(max-width:1439px) {
            margin-bottom: 0;
        }

        @media(max-width:767px) {
            margin-bottom: 10px;
        }
    }
}

.category__slide {
    display: block;
    user-select: none; 
}

.category__img {
    width: 100%;
    height: 413px;
    border-radius: 4px;
    overflow: hidden;

    @media(max-width:767px) {
        height: 350px;
    }

    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: .4s linear;
    }

    @media(min-width:1024px) {
        &:hover {
            img {
                transform: scale(1.1);
            }
        }
    }
}

.category__name {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 61px;

    color: #000;

    @media(max-width:767px) {
        height: 40px;
    }
}