// testimonials


.testimonials {
    margin-bottom: 60px;

    @media(max-width:1023px) {
        margin-bottom: 35px;
    }
}

.testimonials__inner {
    display: flex;

    @media(max-width:1023px) {
        flex-direction: column;
    }
}

.testimonials__stats {
    min-width: 192px;
    min-height: 225px;
    padding: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    background: #FFFFFF;
    border: 1px solid #BABEC1;
    border-radius: 4px;

    @media(max-width:1439px) {
        min-height: 185px;
        padding: 10px;
    }

    @media(max-width:1023px) {
        min-width: unset;
        width: 100%;
        margin: 0 auto 10px;
    }
}

.testimonials__stats-rating {
    margin-bottom: 10px;
}

.testimonials__stats-text {
    color: #000000;
    margin-bottom: 10px;
}

.testimonials__stats-grade {
    color: #000000;
    margin-bottom: 10px;
}

.testimonials__list {
    width: calc(100% - 191px);
    padding-left: 69.5px;
    padding-right: 102.5px;
    position: relative;

    @media(max-width:1439px) {
        padding-left: 60px;
        padding-right: 60px;
    }

    @media(max-width:1023px) {
        width: 100%;
        padding: 0;
    }
}

.testimonials__slider {
    padding: 0 1px;

    @media(max-width:1023px) {
        padding: 0;
    }
}

.testimonials__slide {
    min-height: 225px;
    padding: 30px;

    background: #FFFFFF;
    border: 1px solid #BABEC1;
    border-radius: 4px;

    @media(max-width:1439px) {
        padding: 10px;
        min-height: 185px;
    }
}

.testimonials__rating {
    height: 29px;
    display: flex;
    align-items: center;
    margin-bottom: 20px;

    position: relative;

    @media(max-width:1439px) {
        margin-bottom: 10px;
    }

    &::before {
        content: '';
        width: 29px;
        height: 29px;
        background-image: url("../img/testimonials-icon.svg");

        position: absolute;
        top: 0;
        right: 0;
    }
}

.testimonials__time {
    display: block;
    margin-bottom: 20px;

    font-weight: 800;
    font-size: 14px;
    line-height: 120%;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: #BABEC1;

    @media(max-width:1439px) {
        margin-bottom: 10px;
    }

    @media(max-width:1023px) {
        font-size: 12px;
    }
}

.testimonials__title {
    margin-bottom: 20px;
    color: #000000;

    @media(max-width:1439px) {
        margin-bottom: 10px;
    }
}

.testimonials__descr {
    color: #000000;
}

.testimonials-button-next,
.testimonials-button-prev {
    cursor: pointer;

    position: absolute;
    top: 50%;
    z-index: 2;
    transform: translateY(-50%);

    svg {
        width: 9.5px;
        height: 19px;
    }

    @media(max-width:1023px) {
        display: none;
    }
}

.testimonials-button-next {
    right: 63px;

    @media(max-width:1439px) {
        right: 30px;
    }
}

.testimonials-button-prev {
    left: 30px;
}