.ar-page {
    margin-top: 50px;
    margin-bottom: 70px;

    .container {
        margin-top: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
    }
}


.ar-page__code {
    margin-top: 30px;
}

.ar-page__img {
    margin-top: 20px;
}

.ar-page__bottom {
    margin-top: 35px;
    background: #F7F6F6;
    border-radius: 4px;
    padding: 20px;

    li {
        font-weight: 400;
        font-size: 18px;
        line-height: 150%;
        letter-spacing: 0.08em;
        color: #2D2D2D;
        margin-bottom: 10px;
        display: flex;
        align-items: center;

        &::before {
            content: '';
            width: 8px;
            min-width: 8px;
            height: 8px;
            border-radius: 50%;
            display: block;
            background: #2D2D2D;
            margin-right: 18px;
            transform: translateY(2px);
        }
    }
}

@media (max-width: 900px) {
    .ar-page__bottom {
        width: 100%;
    }
}