.contact {
    margin-top: 25px;
}

.contact__title {
    font-size: 50px;
}

.contact__wrapper {
    display: flex;
    justify-content: space-between;
    margin-top: 50px;
}

.contact-form {
    max-width: 812px;
    margin-right: 50px;

    .input-wrap {
        margin-top: 18px;
    }

    .select-wrap {
        margin-top: 18px;
        max-width: 453px;
    }

    .texarea-wrap {
        margin-top: 18px;
    }
}

.contact-form__title {
    font-weight: 800;
    font-size: 23px;
    line-height: 120%;
    letter-spacing: 0.04em;
    color: #000000;
}

.contact-form__descr {
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;
    letter-spacing: 0.08em;
    color: #000000;
    margin-top: 20px;

}


.contact-form__btn {
    margin-top: 18px;
}

.contact-info {
    max-width: 453px;
}

.contact-info-item {
    padding: 20px;

    p {
        font-weight: 400;
        font-size: 18px;
        line-height: 150%;
        letter-spacing: 0.08em;
        color: #000000;
        margin-bottom: 5px;
    }

    a {
        font-weight: 400;
        font-size: 18px;
        line-height: 150%;
        letter-spacing: 0.08em;
        color: #004080;
        display: block;
        transition: all 0.3s ease-in-out;

        &:hover {
            text-shadow: .5px 0 0 currentColor;
        }
    }
}

.contact-info-item__title {
    font-weight: 800;
    font-size: 23px;
    line-height: 120%;
    letter-spacing: 0.04em;
    color: #000000;
    margin-bottom: 20px;
}

.map {
    margin-top: 63px;

    img {
        width: 100%;
        max-height: 100%;
        object-fit: cover;
        border-radius: 4px;
    }
}

.contact-img-bottom {
    width: 100%;
    margin-top: 80px;
}

@media (max-width: 900px) {
    .contact__wrapper {
        flex-direction: column;
    }

    .contact-form {
        margin-right: 0;
        margin-bottom: 30px;
    }
}

@media (max-width: 580px) {
    .contact-form__descr {
        font-size: 16px;
    }

    .contact__title {
        font-size: 35px;
    }

    .contact__wrapper {
        margin-top: 30px;
    }

    .contact-info-item {
        padding: 10px;
        padding-right: 0;
        padding-left: 0;
    }

    .contact-info-item__title {
        font-size: 20px;

    }

    .contact-info-item p {
        font-size: 16px;
    }

    .contact-info-item a {
        font-size: 16px;
    }

    .map {
        margin-top: 30px;
        width: calc(100% + 40px);
        transform: translateX(-20px);
    }

    .contact-img-bottom {
        margin-top: 30px;
    }
}