.filter {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 10px;

    @media(max-width:1023px) {
        align-items: flex-end;
    }

    &--subcategory {
        margin-bottom: 62px;

        @media(max-width:767px) {
            margin-bottom: 25px;
        }
    }
}

.filter__list {
    display: flex;
    align-items: center;

    @media(max-width:1023px) {
        flex-direction: column;
        align-items: flex-start;
    }
}

.filter__item {
    padding-bottom: 10px;
    cursor: pointer;
    position: relative;

    &+& {
        margin-left: 18px;

        @media(max-width:1023px) {
            margin-left: 0;
            margin-top: 15px;
        }
    }

    &:hover {
        .filter__dropdown {
            opacity: 1;
            visibility: visible;
        }

        .filter__btn::before {
            transform: rotateX(180deg) translateY(50%);
        }
    }
}

.filter__btn {
    display: flex;
    align-items: center;

    font-weight: 800;
    font-size: 15px;
    line-height: 120%;
    letter-spacing: 0.08em;
    color: #2D2D2D;

    padding-right: 26px;
    position: relative;

    &--sale {
        color: #E7312F;
    }

    &::before {
        content: '';
        width: 8px;
        height: 4px;

        background-image: url("../img/arrow.svg");
        background-size: contain;
        background-repeat: no-repeat;

        transition: .3s linear;

        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
    }
}

.filter__btn-value {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 10px;
    padding: 1px 9px;

    border-radius: 50%;
    background: #E7312F;
    border-radius: 20px;

    font-weight: 800;
    font-size: 15px;
    line-height: 150%;
    letter-spacing: 0.08em;
    text-transform: uppercase;
    color: #FFFFFF;
}

.filter__value {
    padding: 5px 15px;

    color: #BABEC1;

    background: #F7F6F6;
    border-radius: 20px;
}

.filter__dropdown {
    padding: 10px;

    background: #FFFFFF;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
    border-radius: 4px;
    overflow: hidden;
    opacity: 0;
    visibility: hidden;
    transition: .3s linear;

    position: absolute;
    top: 100%;
    left: 0;
    z-index: 3;
}

.filter__checkbox {
    white-space: nowrap;

    &+& {
        margin-top: 20px;
    }

    &.empty {
        opacity: 0.3;
        pointer-events: none;
    }
}