.article-page {
    margin-top: 34px;
    margin-bottom: 100px;

    .container {
        max-width: 985px;

        div {
            span {
                font-weight: 800;
                font-size: 12px;
                line-height: 150%;
                letter-spacing: 0.08em;
                color: #BABEC1;

                &:first-child {
                    margin-right: 20px;
                    padding-right: 20px;
                    border-right: 1px solid #BABEC1;
                }
            }

            h3 {
                background: #F7F6F6;
                border-radius: 4px;
                padding: 20px;
                width: 100%;
                text-align: center;
            }

            a {
                margin-right: 20px;
            }
        }

        h1 {
            font-weight: 800;
            font-size: 50px;
            line-height: 110%;
            letter-spacing: 0.08em;
            color: #2D2D2D;
            margin: 30px 0;
        }

        p {
            font-weight: 400;
            font-size: 18px;
            line-height: 150%;
            letter-spacing: 0.08em;
            color: #2D2D2D;
            margin-bottom: 30px;
        }

        >img {
            width: 100%;
            margin-bottom: 30px;
            border-radius: 4px;
        }

        h3 {
            font-weight: 800;
            font-size: 23px;
            line-height: 120%;
            letter-spacing: 0.08em;
            color: #2D2D2D;
            margin-bottom: 30px;
        }

        h2 {
            font-weight: 800;
            font-size: 35px;
            line-height: 120%;
            letter-spacing: 0.08em;
            color: #2D2D2D;
            margin-bottom: 30px;
        }

        ul,
        ol {
            margin-bottom: 30px;

            li {
                font-weight: 400;
                font-size: 18px;
                line-height: 150%;
                letter-spacing: 0.08em;
                color: #2D2D2D;
            }
        }

        ul {
            li {
                position: relative;
                padding-left: 18px;
                margin-bottom: 10px;

                &:last-child {
                    margin-bottom: 0;
                }

                &::before {
                    position: absolute;
                    content: '';
                    background: #2D2D2D;
                    width: 8px;
                    height: 8px;
                    border-radius: 50%;
                    left: 0;
                    top: 10px;
                }
            }
        }

        ol {
            li {
                margin-left: 22px;
            }
        }
    }


}


@media (max-width: 1200px) {
    .article-page .container h1 {
        font-size: 40px;
    }

    .article-page .container h2 {
        font-size: 30px;
    }

    .article-page .container h3 {
        font-size: 20px;
    }
}

@media (max-width: 580px) {
    .article-page {
        margin-bottom: 50px;

        .container {
            h1 {
                margin: 20px 0;
                font-size: 35px;
            }

            p {
                font-size: 16px;
                margin-bottom: 20px;
            }

            h2 {
                font-size: 25px;
                margin-bottom: 20px;
            }

            h3 {
                margin-bottom: 20px;
            }

            img {
                margin-bottom: 20px;
            }

            ul,
            ol {
                margin-bottom: 20px;

                li {
                    font-size: 16px;
                }
            }
        }
    }
}