.product-advisory__title {
    color: #2D2D2D;
    text-align: center;
    margin-bottom: 55px;
}


.product-advisory__subtitle {
    margin-bottom: 30px;
    color: #2D2D2D;
    text-align: center;
}

.product-advisory__text {
    max-width: 821px;
    margin: 0 auto 60px;

    text-align: center;
    color: #000000;

    @media(max-width:767px) {
        margin-bottom: 30px;
    }
}

.product-advisory__img {
    max-width: 960px;
    margin: 0 auto 60px;

    border-radius: 4px;
    overflow: hidden;

    @media(max-width:767px) {
        height: 400px;
        margin-bottom: 30px;
    }
}