.mini-card {
    margin-bottom: 60px;
}


.mini-card__wrapper {
    margin-top: 60px;

    @media (min-width: 580px) {
        .swiper-wrapper {
            display: flex;
            justify-content: center;
            gap: 30px;
            flex-wrap: wrap;
        }

        .swiper-slide {
            width: 330px;
        }

        .highlights__progressbar {
            display: none;
        }
    }
}

@media (max-width: 1105px) {
    .mini-card__wrapper {
        @media (min-width: 580px) {
            .swiper-slide {
                width: 237px;
            }

            .highlights__progressbar {
                display: none;
            }
        }
    }

}

@media (max-width: 580px) {
    .mini-card__wrapper {
        margin-top: 20px;

        .swiper {
            padding-bottom: 32px;
        }
    }
}