.catalog {
    margin-bottom: 60px;

    @media(max-width:767px) {
        margin-bottom: 35px;
    }
}

.catalog__title {
    text-align: center;
    color: #2D2D2D;

    margin-bottom: 54px;
}

.catalog__sale-title {
    text-align: center;
    color: #2D2D2D;

    margin-bottom: 50px;

    @media(max-width:1023px) {
        margin-bottom: 30px;
    }
    
    span {
        color: #E7312F;
    }
}

.catalog__list {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -15px;

    @media(max-width:767px) {
        margin: 0 -7.5px;
    }

    .product {
        width: calc(100% / 4 - 30px);
        margin: 0 15px 27px;

        @media(min-width:1024px) {
            &:hover {
                transform: scale(1.08);
            }
        }

        @media(max-width:1439px) {
            width: calc(100% / 3 - 30px);
        }

        @media(max-width:1023px) {
            width: calc(100% / 2 - 30px);
        }

        @media(max-width:767px) {
            width: calc(100% / 2 - 15px);
            margin: 0 7.5px 17px;
        }

        @media(max-width:515px) {
            width: calc(100% - 15px);
        }
    }
}

.card-intermediate {
    display: flex;
    width: calc(100% / 2 - 30px);
    margin: 0 15px 27px;

    border-radius: 4px;
    overflow: hidden;
    background: #004080;

    @media(max-width:1439px) {
        width: calc(100% / 3 * 2 - 30px);
    }

    @media(max-width:1023px) {
        width: calc(100% - 30px);
    }

    @media(max-width:767px) {
        width: calc(100% - 15px);

        margin: 0 7.5px 17px;
    }

    @media(max-width:515px) {
        height: 400px;
    }
}

.card-intermediate__content {
    width: 50%;
    padding: 0 19px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    @media(max-width:515px) {
        width: 100%;
    }
}

.card-intermediate__title {
    text-align: center;
    color: #FFFFFF;

    margin-bottom: 20px;
}

.card-intermediate__img {
    width: 50%;

    @media(max-width:515px) {
        display: none;
    }
}

.excellence-card {
    display: flex;
    width: calc(100% / 2 - 30px);
    margin: 0 15px 27px;

    border-radius: 4px;
    overflow: hidden;
    background: #2D2D2D;

    @media(max-width:1439px) {
        width: calc(100% / 3 * 2 - 30px);
    }

    @media(max-width:1023px) {
        width: calc(100% - 30px);
    }

    @media(max-width:767px) {
        width: calc(100% - 15px);

        margin: 0 7.5px 17px;
    }

    @media(max-width:515px) {
        flex-direction: column-reverse;

        position: relative;
        padding-top: 50px;
    }
}

.excellence-card__info {
    width: 50%;
    padding: 20px;

    @media(max-width:515px) {
        width: 100%;
        padding: 15px;
    }
}

.excellence-card__promo {
    padding: 5px 15px;
    margin-bottom: 50px;
    width: fit-content;

    color: #FFFFFF;

    background: #CA6E19;
    border-radius: 20px;

    @media(max-width:515px) {
        margin-bottom: 0;

        position: absolute;
        left: 20px;
        top: 20px;
    }
}

.excellence-card__title {
    color: #F7F6F6;
    margin-bottom: 20px;

    @media(max-width:515px) {
        margin-bottom: 15px;
        text-align: center;
    }
}


.excellence-card__descr {
    color: #F7F6F6;
    margin-bottom: 20px;

    @media(max-width:515px) {
        margin-bottom: 15px;
        text-align: center;
    }
}

.excellence-card__price {
    margin-bottom: 20px;

    font-weight: 800;
    font-size: 18px;
    line-height: 100%;
    letter-spacing: 0.1em;
    color: #F7F6F6;

    @media(max-width:515px) {
        margin-bottom: 15px;
        text-align: center;
    }
}

.excellence-card__btn {
    @media(max-width:515px) {
        margin: 0 auto;
    }
}

.excellence-card__img {
    width: 50%;
    max-height: 457px;

    @media(max-width:515px) {
        width: 100%;
        max-height: 330px;
    }

    img {
        object-fit: contain;
    }
}