// articles


.articles {
    margin-bottom: 44px;
}

.articles__sec-title {
    margin-bottom: 30px;
    text-align: center;
}

.articles__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 30px;
    margin-bottom: 20px;

    @media(max-width:1439px) {
        grid-gap: 15px;
    }

    @media(max-width:767px) {
        grid-template-columns: repeat(1, 1fr);
        grid-template-rows: repeat(3, 1fr);
    }
}

.articles__item {
    width: 100%;
    display: flex;
    flex-direction: column;
    padding-bottom: 11px;

    &:hover {
        .articles__img {
            img {
                transform: scale(1.1);
            }
        }
    }
}

.articles__img {
    width: 100%;
    height: 306px;
    margin-bottom: 20px;
    border-radius: 4px;
    overflow: hidden;

    @media(max-width:1439px) {
        margin-bottom: 10px;
    }

    @media(max-width:1023px) {
        height: 160px;
    }

    @media(max-width:767px) {
        height: 250px;
    }

    @media(max-width:424px) {
        height: 190px;
    }

    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
        transition: all 0.3s linear;
    }
}

.articles__info {
    display: flex;
    margin-bottom: 20px;

    @media(max-width:1439px) {
        margin-bottom: 10px;
    }
}

.articles__time {
    font-weight: 800;
    font-size: 15px;
    line-height: 150%;
    letter-spacing: 0.08em;
    text-transform: uppercase;
    color: #BABEC1;

    @media(max-width:1919px) {
        font-size: 13px;
    }
}

.articles__category {
    font-weight: 800;
    font-size: 15px;
    line-height: 150%;
    letter-spacing: 0.08em;
    text-transform: uppercase;
    color: #BABEC1;

    margin-left: 20px;
    padding-left: 20px;

    position: relative;

    @media(max-width:1919px) {
        font-size: 13px;
        margin-left: 10px;
        padding-left: 10px;
    }

    &::before {
        content: '';
        height: 17px;
        width: 1px;
        background-color: #BABEC1;

        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
    }
}

.articles__title {
    min-height: 116px;
    margin-bottom: 20px;
    color: #2D2D2D;

    @media(max-width:1919px) {
        min-height: 56px;
    }

    @media(max-width:1439px) {
        margin-bottom: 10px;
    }

    @media(max-width:767px) {
        min-height: unset;
    }
}

.articles__descr {
    min-height: 135px;
    margin-bottom: auto;
    padding-bottom: 20px;
    color: #000000;

    @media(max-width:1919px) {
        min-height: 125px;
    }

    @media(max-width:1439px) {
        padding-bottom: 10px;
    }
}

.articles__link {
    width: fit-content;
    overflow: hidden;
    color: #2D2D2D;

    position: relative;

    &::before {
        content: '';
        height: 1px;
        background-color: #2D2D2D;

        transition: .2s linear;

        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
    }

    @media(min-width:1024px) {
        &:hover::before {
            transform: translateX(100%);
        }
    }
}

.articles__blog-link {
    margin: 0 auto;
}