// highlights


.highlights {
    margin-bottom: 66px;

    @media(max-width:1023px) {
        margin-bottom: 36px;
    }
}

.highlights__inner {
    position: relative;
    overflow: hidden;

    .swiper-wrapper {
        margin-bottom: 30px;

        @media(max-width:767px) {
            margin-bottom: 20px;
        }
    }

    .highlights__item {
        &:nth-child(even) {
            .highlights__info {
                background: #2D2D2D;
            }
        }
    }
}

.highlights-main__title {
    text-align: center;
    margin-bottom: 30px;
}

.highlights__item {
    display: block;
    border-radius: 4px;
    overflow: hidden;
    user-select: none;

    &.deactivate {
        pointer-events: none;
        position: relative;

        &::before {
            content: '';
            background-color: #fff;
            opacity: .7;

            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
            z-index: 2;
        }
    }

    @media(min-width:1024px) {
        &:hover {
            .highlights__img img {
                transform: scale(1.1);
            }
        }
    }
}

.highlights__img {
    width: 100%;
    height: 308px;
    overflow: hidden;

    @media(max-width:767px) {
        height: 248px;
    }

    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;

        transition: transform .4s linear;
    }
}

.highlights__info {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 252px;
    padding: 20px;

    background: #E7312F;

    @media(max-width:1919px) {
        height: 222px;
    }
}

.highlights__title {
    color: #fff;
}

.highlights__text {
    color: #F7F6F6;
}