.stock-ofer {
    margin-bottom: 80px;
}

.stock-ofer__wrapper {
    display: flex;
    position: relative;
}

.stock-ofer__left {
    width: 457px;
    min-width: 457px;
    padding: 100px 30px;
    background: #E7312F;
    border-radius: 0px 0px 4px 4px;
    z-index: 2;
}

.stock-ofer__title {
    font-weight: 800;
    font-size: 35px;
    line-height: 120%;
    letter-spacing: 0.08em;
    color: #FFFFFF;
}

.stock-ofer__descr {
    font-weight: 400;
    font-size: 18px;
    line-height: 150%;
    letter-spacing: 0.08em;
    color: #FFFFFF;
    margin-top: 10px;
}

.stock-ofer__btn {
    margin-top: 10px;
}


.stock-ofer__bg {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
}