.product {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 15px 15px 15.5px;

    transition: .3s linear;
    will-change: transform;
    
    position: relative;

    &::before {
        content: '';

        background: #F7F6F6;
        border-radius: 4px;

        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        z-index: -1;
    }

    @media(min-width:1024px) {
        &:hover {
            transform: scale(1.1);
        }
    }

    @media(max-width:1439px) {
        padding: 10px;
    }

    &--set {
        padding-top: 57px;

        .product__model {
            margin-bottom: 56px;

            @media(max-width:1023px) {
                margin-bottom: 59px;
            }
        }

        .product__img {
            @media(max-width:1023px) {
                max-height: 150px;
            }
        }
    }

    &--sale {
        padding-top: 57px;
    }
}

.product__img {
    width: 100%;
    height: 100%;
    max-height: 291px;
    margin-bottom: 10px;

    img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: contain;
    }
}

.product__name {
    margin-bottom: 10px;

    text-align: center;
    font-weight: 800;
    font-size: 18px;
    line-height: 120%;
    letter-spacing: 0.04em;
    color: #2D2D2D;

    @media(max-width:767px) {
        font-size: 15px;
    }
}

.product__model {
    text-align: center;
    margin-bottom: auto;
    padding-bottom: 10px;
}

.product__price {
    text-align: center;
}

.product__price-new {
    color: #E7312F;
}

.product__price-old {
    margin-left: 5px;

    font-weight: 400;
    font-size: 14px;
    line-height: 150%;
    letter-spacing: 0.1em;
    text-decoration-line: line-through;
    color: #2D2D2D;
}

.product__discount {
    padding: 5px 15px;

    background: #E7312F;
    border-radius: 20px;
    color: #FFFFFF;

    position: absolute;
    top: 15px;
    left: 18px;

    @media(max-width:1023px) {
        left: 15px;
    }
}